body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
@font-face {
  font-family: "OpenSans-Light";
  src: url(/epizodeslist_with_calendar/static/media/OpenSans-Light.9ff12f69.ttf);
}
@font-face {
  font-family: "OpenSans-Regular";
  src: url(/epizodeslist_with_calendar/static/media/OpenSans-Regular.d7d5d458.ttf);
}
@font-face {
  font-family: "OpenSans-SemiBold";
  src: url(/epizodeslist_with_calendar/static/media/OpenSans-SemiBold.e1c83f94.ttf);
}
@font-face {
  font-family: "OpenSans-Bold";
  src: url(/epizodeslist_with_calendar/static/media/OpenSans-Bold.f5331cb6.ttf);
}


/*============================== Adaptive ==============================*/
@media(min-width: 0px) and (max-width: 750px) {
  .App {
    font-family: "OpenSans-Light";
    color: rgb( 51, 51, 51 );      
  }
  .App-header {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f5f5f5;
    font-family: "OpenSans-Regular";
    letter-spacing: 1px
  }
  .App-header h1 {
    font-size: medium;
  }

  .App-main {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .content img {
    width: 40%;
    padding-top: 20px;
  }

  span {
    padding: 15px 0;
    text-align: center;
    line-height: 1.067;
  }

  .calendar {
    width: 100%;
    position: fixed;
    bottom: 0;
  }

  .react-calendar, .react-calendar ::before, .react-calendar ::after {
    border: none !important;
    width: 100% !important;
  }

  .react-calendar__navigation__prev2-button, 
  .react-calendar__navigation__next2-button,
  .react-calendar__month-view__weekdays {
    display: none !important;
  }

  .react-calendar__navigation {
    background-color:#f1f1f1 !important;
    margin: 0 !important;
  }

  .react-calendar__navigation__arrow {
    font-size: 30px;
    color: #8c939a;
  }
  
  .react-calendar__navigation button:enabled:hover, .react-calendar__navigation button:enabled:focus {
    background-color: #f1f1f1 !important;
  }

  .react-calendar__month-view__days__day {
    color: rgb( 51, 51, 51 );
  }

  .react-calendar__month-view__days .react-calendar__month-view__days__day--weekend {
    color: rgb( 51, 51, 51 );
  }

  .react-calendar__month-view__days .react-calendar__month-view__days__day--neighboringMonth {
    color: #cccfd3;
  }

  .react-calendar__tile:enabled:hover {
    border: 1px solid rgb(216, 83, 83);
    background-color: transparent !important;
  }

  .react-calendar__tile:enabled:focus {
    border: 1px solid #e01050;
    background-color: transparent !important;
    color: black;
    font-weight: bold;
  }
  .react-calendar__tile--active {
    background: transparent !important;
    color: black !important;
  }

  .react-calendar .react-calendar__tile {
    border: 1px solid #d0d4d9;
  }

  .react-calendar__tile--now {
    font-weight: bold !important;
  }

  .bt-back {
    position: absolute;
    left: 0;
    vertical-align: middle;
    top: 9px;
    background-color: transparent;
    border: none;
    font-size: 20px;
    color: #e10246;
    cursor: pointer;
  }

  .dayMovieList {
    width: 100%;
  }
}

@media(min-width: 430px) and (max-width: 750px) {
  .content {
    flex-direction: row;
    justify-content: space-around;
  }

  .content img {
    width: 25%;
    padding-top: 5px;
  }

  .react-calendar__tile {
    padding: 0.4em 0.5em !important;
  }
}

/*============================== Adaptive ==============================*/
@media(min-width: 0px) and (max-width: 750px) {
  .card {
    display: flex;
    padding-left: 15px;
    padding-right: 15px;
    height: 120px;
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .card-img {
    width: 30%;
    height: 120px;
    box-shadow: 0 0 5px 2px #8c939a;
  }

  .card-img-top {
    width: 100%;
    height: inherit;
    cursor: pointer;
  }

  .card-body {
    position: relative;
    width: 70%;
    padding-bottom: 49px;
    padding-left: 10px;
  }

  .card-title {
    margin: 0;
  }

  .card-premiered {
    font-family: "OpenSans-SemiBold";
    color: #b4b4b4;
    font-size: 12px;
    font-weight: 400;
    margin-top: 0;
  }

  .card-info {
    width: 90%;
    position: absolute;
    bottom: 0;
    border-radius: 10px;
    background-color: #f5f5f5;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "OpenSans-SemiBold";
    color: #999999;
    font-size: 12px;
    font-weight: 400;
    height: 40px;
    padding-left: 5px;
    padding-right: 5px;
  }

  .card--info-season {
    margin-right: 5px;
  }
}

@media(min-width: 376px) and (max-width: 500px) {
  .card-img {
    max-width: 100px;
  }

  .card-body {
    width: 80%;
  }

  .card-info {
    width: 60%;
  }
}

@media(min-width: 501px) and (max-width: 750px) {
  .card-img {
    max-width: 100px;
  }

  .card-info {
    width: 45%;
  }
}
.big-image {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  background-color: transparent;
}

.bground {
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0.7;
  position: inherit;
}

.card-img-big {
  width: 200px;
  height: 280px;
  position: inherit;
  cursor: pointer;
}
/*============================== Adaptive ==============================*/
@media(min-width: 0px) and (max-width: 750px) {
  .movieList {
    font-size: 14px;
    font-weight: bold;
    color: #8c939a;
  }

  .movieList-date {
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #d0d4d9;
    color: rgb( 51, 51, 51 );
    font-family: "OpenSans-SemiBold";
    font-weight: 400;
    height: 40px;
    font-size: 13px;
  }

  .card-title {
    color: rgb( 51, 51, 51 );
  }

  .block-bt {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 10px;
  }

  .bt-longList {
    width: 100%;
    background-color: #ffffff;;
    height: 40px;
    border: 2px solid #d6d6d6;
    border-radius: 10px; 
    display: flex;
    justify-content: center;
    align-items: center; 
    margin-bottom: 5px;
    color: #999999;
    font-weight: 400;
    cursor: pointer;
  }

  .bt-arrow {
    padding-left: 2px;
    padding-bottom: 10px; /* Для Firefox */ /* Для IE */
    -webkit-transform: rotate(90deg); /* Для Safari, Chrome, iOS */ /* Для Opera */
    transform: rotate(90deg);
  }
}
